t":["\n.page-pay {\n    &__parent {\n        background: #15101D;\n        border: 2px solid var(--color-secondary);\n        padding: 16px;\n        border-radius: 24px;\n        // flex md:flex-row flex-col md:border-[#3E3B47CC] md:p-4\n        display: flex;\n        flex-direction: row;\n        padding: 16px;\n        margin: 0px;\n    }\n\n    &__banners-mobile {\n        display: none;\n    }\n\n    &__banners-desc {\n        display: block;\n        padding: 0 0px;\n        margin-bottom: 24px;\n    }\n\n    &__title {\n        font-size: 24px;\n        color: var(--color-text);\n        font-weight: 500;\n        font-family: Oswald !important;\n        // text-align: center;\n    }\n\n    &__title2 {\n        font-size: 24px;\n        color: var(--color-text);\n        font-weight: 500;\n        font-family: Oswald !important;\n        // text-align: center;\n    }\n\n    &__desc {\n        text-align: center;\n        font-size: 16px;\n        color: var(--color-text);\n    }\n\n    &__form {\n        margin-bottom: 48px;\n    }\n\n    &__withdraw-methods {\n        margin-bottom: 24px;\n\n    }\n\n    &__comission {\n        span {\n            color: var(--color-text-secondary);\n\n            strong {\n                color: var(--color-text);\n            }\n\n            span {\n                color: var(--color-text);\n            }\n        }\n    }\n\n\n    .withdraw-methods {\n        display: flex;\n        flex-direction: column;\n        //  align-items: center;\n\n\n        &__title {\n            font-size: 24px;\n            color: var(--color-text);\n            font-family: Oswald !important;\n        }\n\n        &__desc {\n            font-size: 16px;\n            color: var(--color-text);\n            // margin-bottom: 0px;\n        }\n\n        &__min-deposite {\n            font-size: 16px;\n            color: var(--color-text-secondary);\n            margin-bottom: 24px;\n        }\n\n        &__items {\n            display: grid;\n            grid-template-columns: repeat(3, 1fr);\n            gap: 16px;\n            height: fit-content;\n            overflow: auto;\n            margin-bottom: 24px;\n            padding: 0px 16px 16px 0;\n\n            &::-webkit-scrollbar {\n                width: 5px;\n                border-radius: 100%;\n            }\n\n            &::-webkit-scrollbar-thumb {\n                border-radius: 10px;\n            }\n\n            &::-webkit-scrollbar-track {\n                border-radius: 10px;\n            }\n        }\n\n        .withdraw-method {\n            width: 100%;\n            height: 160px;\n            border-radius: 8px;\n            padding: 5px;\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n            background: #1f1d28;\n            // border: 2px solid var(--color-secondary);\n            transition: all 0.2s ease;\n\n            &_active {\n                border-color: var(--color-primary);\n                background: #2e2a36;\n            }\n\n            &__img {\n                width: 50%;\n                height: 80px;\n                object-position: center;\n                object-fit: contain;\n\n            }\n\n            &__title {\n                margin-top: auto;\n                font-size: 16px;\n                color: var(--color-text);\n            }\n\n            &__desc {\n                font-size: 14px;\n                color: var(--color-text-secondary);\n            }\n        }\n    }\n\n    .banks {\n        flex-direction: column;\n        align-items: center;\n\n        &__title {\n            font-size: 24px;\n            color: var(--color-text);\n            font-family: Oswald !important;\n        }\n\n        &__desc {\n            font-size: 16px;\n            color: var(--color-text);\n            margin-bottom: 0px;\n            margin-bottom: 24px;\n        }\n\n        &__items {\n            display: grid;\n            grid-template-columns: repeat(3, 1fr);\n            gap: 16px;\n            margin-bottom: 24px;\n        }\n\n        .bank {\n            background: var(--color-form);\n            border-radius: 8px;\n            justify-items: center;\n            width: 100%;\n            height: 160px;\n            border: 1px solid var(--color-form-border);\n            padding: 10px;\n            transition: all 0.2s ease;\n\n            @media (hover: hover) {\n                &:hover {\n                    transform: scale(1.05);\n                }\n            }\n\n            &_active {\n                border: 2px solid var(--color-primary);\n            }\n        }\n    }\n\n    &__button {\n        border-radius: 4px !important;\n        font-family: Oswald !important;\n        text-transform: uppercase;\n        margin-bottom: 24px;\n    }\n\n    &__warning {\n        font-size: 14px;\n        color: var(--color-text-secondary);\n        // text-align: center;\n    }\n\n    &__history {\n        color: var(--color-text);\n        width: 100%;\n        overflow: auto;\n\n        table {\n            min-width: 700px;\n            color: var(--color-text);\n            width: 100%;\n        }\n    }\n\n    .pay_inputs {\n        @apply p-4 border-l border-[#3E3B47CC] w-[100%];\n    }\n}\n\n@media (max-width: 1024px) {\n    .page-pay {\n        &__desc {\n            width: 100%;\n        }\n\n        &__form {\n            width: 100%;\n        }\n\n        &__button {\n            width: 100%;\n        }\n\n        &__parent {\n            margin: 16px;\n        }\n\n        &__banners-mobile {\n            //  w-full md:mb-10 mb-6 aspect-[6/1] px-4\n            display: none;\n        }\n\n        &__banners-desc {\n            padding: 0 16px;\n            display: block;\n            margin-bottom: 0px;\n        }\n\n\n        .withdraw-methods {\n            &__items {\n                grid-template-columns: repeat(3, 1fr);\n                gap: 16px;\n            }\n        }\n\n        &__warning {\n            width: 100%;\n            color: var(--color-text-secondary);\n        }\n\n        .banks {\n            &__items {\n                grid-template-columns: repeat(3, 1fr);\n            }\n        }\n\n        &__history {\n            color: var(--color-text);\n            width: 100%;\n            overflow: auto;\n\n            table {\n                min-width: 700px;\n                width: 100%;\n            }\n        }\n    }\n\n    .form {\n        &__input {\n            padding-left: 8px;\n        }\n    }\n}\n\n@media (max-width: 768px) {\n    .page-pay {\n        &__parent {\n            background: none;\n            border: 0px solid var(--color-secondary);\n            padding: 0px;\n            border-radius: 9px;\n            display: flex;\n            flex-direction: column;\n            padding: 0px;\n            margin: 16px;\n        }\n\n        &__banners-mobile {\n            padding: 0 16px;\n            display: block;\n            margin-bottom: 0px;\n        }\n\n        &__banners-desc {\n            display: none;\n        }\n\n        &__withdraw-methods {\n            margin-bottom: 0px;\n        }\n\n        .withdraw-methods {\n            .withdraw-methods {\n                aspect-ratio: initial;\n\n                &__img {\n                    width: 43px;\n                }\n\n                p {\n                    padding: 16px;\n                }\n\n            }\n\n            &__desc {\n                padding: 0 16px;\n            }\n\n            &__items {\n                display: grid;\n                grid-template-columns: repeat(3, 1fr);\n                gap: 8px;\n                height: 334px;\n                margin-bottom: 16px;\n                padding: 0 16px;\n\n            }\n\n            background: #15101D;\n            border: 2px solid var(--color-secondary);\n            padding: 16px 0;\n            border-radius: 9px;\n        }\n\n        .pay_inputs {\n            padding: 16px;\n            width: 100%;\n            margin-top: 16px;\n            border-radius: 9px;\n            background: #15101D;\n            border-width: 2px !important;\n            border: 2px solid var(--color-secondary);\n        }\n    }\n}\n"],"sourceRoot":""}]);
